  .iosSwitch {
    display: flex;
    align-items: center;
    margin: 10px 0;
    position: absolute;
    right: 10px;
    top: 0;
  }
  .input {
    display: none;
  }
  .switchBody {
    border-radius: 999px;
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    width: 96px;
    height: 48px;
    background-color: #ddd;
    transition: all 0.3s ;
    padding: 5%;
  }
  .toggle {
    border-radius: 50%;
    width:50%;
    height: 90%;
    background-color: white;
    box-sizing: border-box;
    transition: all 0.4s ;
  }
  .active .switchBody {
    background-color: #51D88A;
  }
  .active .toggle {
    transform: translateX(100%);
  }
  .colorDef {
    background-color: #F8C400 !important;
  }